import { Component, OnInit, ViewChild } from '@angular/core';
import {FormGroup} from '@angular/forms';  
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { SelectionModel } from '@angular/cdk/collections';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Employee } from '../employee';
import { EmployeeService } from '../employee.service';
import { AddModalComponent } from '../add-modal/add-modal.component';
import { ConfirmDeleteModalComponent } from '../confirm-delete-modal/confirm-delete-modal.component';
import { UpdateModalComponent } from '../update-modal/update-modal.component';
import { MapModalComponent } from '../map-modal/map-modal.component';


@Component({
  selector: 'app-employee-list',
  templateUrl: './employee-list.component.html',
  styleUrls: ['./employee-list.component.css']
})
export class EmployeeListComponent implements OnInit {
  
  employeesList: Employee[];
  dataSource: any;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  selection = new SelectionModel(true, []);
  selected : Employee[];
  selecting = false;

  constructor(private employeeService: EmployeeService, private snackBar: MatSnackBar, public dialog: MatDialog) { }

  ngOnInit() {
    this.initEmployeesList()
  }

  initEmployeesList() {
    this.employeeService.getEmployeesList().subscribe(data => {
      this.employeesList = data;
      this.dataSource = new MatTableDataSource(this.employeesList);
      this.dataSource.sort = this.sort;
    })
    
  }

  getColumnsToDisplay() {
    return ['select', 'id', 'name', 'floor', 'location','x','y'];
  }

  applyFilter(filterValue: string) {
    
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  toggleSelect() {
    this.selection.clear();
    this.selecting = !this.selecting;
  }

  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

  select(row) {
    this.selection.toggle(row);
  }

  deleteSelected() {
    this.selected = this.selection.selected;
    this.selected.forEach(element => {
      console.log(element.id);
      this.employeeService.deleteEmployee(element.id)  
      .subscribe(  
        data => {  
          console.log(data);
          this.initEmployeesList(); 
        },  
        error => console.log(error)); 
    });
    this.openSnackBar(this.selection.selected.length + " item(s) successfully deleted", "Close");
    this.selection.clear();  
    this.selecting=false;
  }

  openAdd(){
    const dialogRef = this.dialog.open(AddModalComponent, {
      minWidth: '30vw',
      minHeight: '30vh'
    }); 
    dialogRef.afterClosed().subscribe(result => {
      this.initEmployeesList();
      //this.openSnackBar("Employee successfully added", "Close");
    });
  }

  openDeleteConfirm(){
    const dialogRef = this.dialog.open(ConfirmDeleteModalComponent, {
      position: {top:'10%'},
      autoFocus: false,
      data: {length:this.selection.selected.length}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      if (result)
        this.deleteSelected();

      this.initEmployeesList();
    });
  }

  deleteOne(employee) {
    this.selection.clear();
    this.select(employee);
    this.openDeleteConfirm();
  }

  setToUpdateEmployee(id: number) {  
    this.employeeService.getEmployee(id).subscribe(  
        data => {  
          //console.log(data);
          this.openUpdate(data);
        },  
        error => console.log(error));     
  }  

  openUpdate(data:Employee) {
    const dialogRef = this.dialog.open(UpdateModalComponent, {
      minWidth: '30vw',
      minHeight: '30vh',
      data: {employee:data}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      this.initEmployeesList();
      if(result)
        this.openSnackBar("Employee successfully updated", "Close");
    });
  }

  openMap(employee: Employee) {
    const dialogRef = this.dialog.open(MapModalComponent, {
      
      autoFocus: false,
      data: {editing: false, employee: employee}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      //console.log(result);
      this.initEmployeesList();
    });
  }

}
