import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MatDialog} from '@angular/material';
import { Employee } from '../employee';
import { EmployeeService } from '../employee.service';
import { MapModalComponent } from '../map-modal/map-modal.component';

@Component({
  selector: 'app-add-modal',
  templateUrl: './add-modal.component.html',
  styleUrls: ['./add-modal.component.css']
})
export class AddModalComponent implements OnInit {
  //@Output()
  //addEvent = new EventEmitter();

  employee: Employee;
  isAdded: boolean;
  //locations: string[] = ['North', 'South', 'West', 'East'];
  //selectedLocation : string;
  floors: string[] = ['0', '1', '2', '3', '4'];
  selectedFloor : string;

  addEmployeeForm : FormGroup;

  constructor(private employeeService: EmployeeService, public dialogRef: MatDialogRef<AddModalComponent>, public dialog: MatDialog) { }

  ngOnInit() {
    this.addEmployeeForm = new FormGroup({
      name : new FormControl('' , [Validators.required] ), 
      floor : new FormControl('' , ),
      location : new FormControl({value:'', disabled:true} , ),
      xCoord: new FormControl({value:'', disabled:true} ,),
      yCoord: new FormControl({value:'', disabled:true} ,),
    });
  }

  get name() {
    return this.addEmployeeForm.get('name');
  }

  get floor() {
    return this.addEmployeeForm.get('floor');
  }

  get location() {
    return this.addEmployeeForm.get('location');
  }

  get xCoord() {
    return this.addEmployeeForm.get('xCoord');
  }

  get yCoord() {
    return this.addEmployeeForm.get('yCoord');
  }

  onFloorChange() {
    console.log(this.selectedFloor);
    //if(this.selectedFloor == undefined) {
      this.addEmployeeForm.get('location').reset();
      this.addEmployeeForm.get('xCoord').reset();
      this.addEmployeeForm.get('yCoord').reset();
    //}
  }

  addEmployee() {
    this.employee = new Employee();
    this.employee.name = this.name.value;
    this.employee.floor = this.floor.value;
    this.employee.location = this.location.value;
    this.employee.xCoord = this.xCoord.value;
    this.employee.yCoord = this.yCoord.value;
    console.log(this.addEmployeeForm.value);

    this.employeeService.addEmployee(this.employee).subscribe(
      data => {
        console.log(data);
        //this.addEvent.emit();
        //this.initSongsList();
      }, 
      error => console.log(error));
    this.isAdded=true;
  }

  resetAdd(){  
    this.isAdded=false;  
    this.addEmployeeForm.reset();
  }

  openMap() {
    const dialogRef = this.dialog.open(MapModalComponent, {
  
      autoFocus: false,
      data: {editing: true, floor: this.floor.value, location: this.location.value}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      if (result!=undefined) {
        var ret = <Array<string>>result;
        console.log(ret);
        this.location.patchValue(ret[0]);
        this.xCoord.patchValue(ret[1]);
        this.yCoord.patchValue(ret[2]);
      }
    });
  }
}
