import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
declare var $ : any;
import { Employee } from '../employee';

@Component({
  selector: 'app-map-modal',
  templateUrl: './map-modal.component.html',
  styleUrls: ['./map-modal.component.css']
})
export class MapModalComponent implements OnInit {
  config = {
    fade: true,
    alwaysOn: false,
    neverOn: false,

    // fill
    fill: true,
    fillColor: '#dde8f4',
    fillOpacity: 0.5,

    // stroke
    stroke: true,
    strokeColor: '#0676f4',
    strokeOpacity: 1,
    strokeWidth: 2,

    // shadow:
    shadow: true,
    shadowColor: '#000000',
    shadowOpacity: 0.8,
    shadowRadius: 10
  };
  employee: Employee;
  floor: string;
  xCoord: number;
  yCoord: number;
  //coords: number[];
  enabledImg: string;
  editing: boolean;
  locations: string[];
  selectedLocation: string;
  result: string[] = new Array();

  constructor(public dialogRef: MatDialogRef<MapModalComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit() {
    this.editing = this.data.editing;
    this.employee = this.data.employee;
    if(this.employee != null) {
      this.xCoord = parseFloat(this.employee.xCoord);
      this.yCoord = parseFloat(this.employee.yCoord);
    }
    if(!this.editing) {
      this.floor = this.employee.floor;
      this.selectedLocation = this.employee.location;
    } else {
      this.floor = this.data.floor;
      this.selectedLocation = this.data.location;
    }

    switch (this.floor) {
      case '0':
        this.locations = ['North0', 'South0', 'West0', 'East0'];
        this.enabledImg = '0';
        break;
      
      case '1':
        this.locations = ['North1', 'South1', 'West1', 'East1'];
        this.enabledImg = '1';
        break;
        
      case '2':
        this.locations = ['North2', 'South2', 'West2', 'East2'];
        this.enabledImg = '2';
        break;

      case '3':
        this.locations = ['North3', 'South3', 'West3', 'East3'];
        this.enabledImg = '3';
        break;

      case '4':
        this.locations = ['North4', 'South4', 'West4', 'East4'];
        this.enabledImg = '4';
        break;
    
      default:
        break;
    }

    var _this=this;
    $(document).ready(function() {
      
      //make image map responsive
      $('img[usemap]').rwdImageMaps();

      var height:number;
      var width:number;
      setTimeout(function(){
        height = $('img').height();
        width = $('img').width();
        console.log(height+"x"+width);
        
        //show map marker from coordinates (if exists)
       if(!isNaN(_this.xCoord) && !isNaN(_this.yCoord)) {
        //var offset = $('#mapContainer').offset();
        //$('.marker').remove();
        $('#mapContainer').append(
          $('<div class="marker glyphicon glyphicon-map-marker "></div>').css({
            position: 'absolute',
            top:  (_this.yCoord*height-14) + 'px',
            left:  (_this.xCoord*width-7) + 'px',
          })
        );
     }
     
      

      }, 200);

       if(_this.editing) {
        $('map').click(function(e) {
          //save the coordinates (in the image) of the mouse click
          var offset = $('#mapContainer').offset();
          _this.xCoord= (e.pageX - offset.left) / width;
          _this.yCoord = (e.pageY - offset.top) / height;
          _this.result[1] =_this.xCoord.toString();
          _this.result[2] =_this.yCoord.toString();
          console.log(_this.result);
          $('.marker').remove();
          $('#mapContainer').append(
            $('<div class="marker glyphicon glyphicon-map-marker "></div>').css({
            position: 'absolute',
            top:  (_this.yCoord*height-14) + 'px',
            left:  (_this.xCoord*width-7) + 'px',
            })
          ); 
        });
      } 
    });
  }

  close() {
    this.dialogRef.close();
    //console.log(this.result);
  }

  //set location from coords
  setLocation(location: string) {
    if (this.editing) {
      this.selectedLocation = location;
      this.result[0]=location;
    }
  }

  onLocationChange() {
    $('.marker').remove();
    //reset coords to null, keep location
    this.result = [this.selectedLocation];
    console.log(this.result);
  }


}
