import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog} from '@angular/material';
import { Employee } from '../employee';
import { EmployeeService } from '../employee.service';
import { MapModalComponent } from '../map-modal/map-modal.component';

@Component({
  selector: 'app-update-modal',
  templateUrl: './update-modal.component.html',
  styleUrls: ['./update-modal.component.css']
})
export class UpdateModalComponent implements OnInit {

  employee: Employee;
  toUpdateEmployee: Employee;
  //locations: string[] = ['North', 'South', 'West', 'East'];
  //selectedLocation : string;
  floors: string[] = ['0', '1', '2', '3', '4'];
  selectedFloor : string;

  updateEmployeeForm: FormGroup;

  constructor(private employeeService: EmployeeService, public dialogRef: MatDialogRef<UpdateModalComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: any, public dialog: MatDialog) { }

  ngOnInit() {
    this.toUpdateEmployee = this.data.employee;
    this.selectedFloor = this.toUpdateEmployee.floor;
    console.log(this.toUpdateEmployee);
    this.updateEmployeeForm = new FormGroup({
      name : new FormControl(this.toUpdateEmployee.name , [Validators.required] ),  
      floor: new FormControl(this.toUpdateEmployee.floor , ),
      location : new FormControl({value:this.toUpdateEmployee.location, disabled:true} , ),
      xCoord: new FormControl({value:this.toUpdateEmployee.xCoord, disabled:true} ,),
      yCoord: new FormControl({value:this.toUpdateEmployee.yCoord, disabled:true} ,),
    });
  }

  get name() {
    return this.updateEmployeeForm.get('name');
  }

  get floor() {
    return this.updateEmployeeForm.get('floor');
  }

  get location() {
    return this.updateEmployeeForm.get('location');
  }

  get xCoord() {
    return this.updateEmployeeForm.get('xCoord');
  }

  get yCoord() {
    return this.updateEmployeeForm.get('yCoord');
  }

  onFloorChange() {
    console.log(this.selectedFloor);
    //if(this.selectedFloor == undefined) {
      this.updateEmployeeForm.get('location').reset();
      this.updateEmployeeForm.get('xCoord').reset();
      this.updateEmployeeForm.get('yCoord').reset();
    //}
  }

  updateEmployee() {
    this.employee = new Employee();
    this.employee.name = this.name.value;
    this.employee.floor = this.floor.value;
    this.employee.location = this.location.value;
    this.employee.xCoord = this.xCoord.value;
    this.employee.yCoord = this.yCoord.value;
    console.log(this.updateEmployeeForm.value);


    this.employeeService.updateEmployee(this.toUpdateEmployee.id, this.employee).subscribe(
      data => {
        console.log(data);
        //this.updateEvent.emit();
        //this.initSongsList();
      }, 
      error => console.log(error));
  }

  resetUpdate(){   
    this.updateEmployeeForm.reset();
  }

  openMap() {
    const dialogRef = this.dialog.open(MapModalComponent, {
      autoFocus: false,
      data: {editing: true, employee:this.toUpdateEmployee, floor: this.floor.value, location: this.location.value}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      if (result!=undefined) {
        var ret = <Array<string>>result;
        console.log(ret);
        this.location.patchValue(ret[0]);
        this.xCoord.patchValue(ret[1]);
        this.yCoord.patchValue(ret[2]);
      }
    });
  }

}
